    
<template>
  <v-content class="ma-0 pa-0">
  <h1> Welcome back "{{ $store.state.auth.user.displayName }}"</h1>
  <v-row>
    <v-col>
      <v-card>
        <v-card-title>Sales</v-card-title>
        <v-card-actions>
          <v-btn color="green lighten-3" @click="openDialog('sale')"><v-icon>mdi-plus</v-icon>Add New</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
    <v-col>
      <v-card>
        <v-card-title>Retentions</v-card-title>
        <v-card-actions>
          <v-btn color="green lighten-3" @click="openDialog('retention')"><v-icon>mdi-plus</v-icon>Add New</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
  <v-dialog v-model="dialog.value" fullscreen hide-overlay transition="dialog-bottom-transition">
    <v-card>
      <app-item :collection-name="dialog.collection" @close="dialog.value = false" @save="newSave"/>
    </v-card>
  </v-dialog>
  </v-content>

</template>
<script>
// import { mapActions } from 'vuex'

import AppItem from '../components/Item'
export default {
  name: 'UserDashboard',
  components: {
    AppItem
  },
  data () {
    return {
      dialog: {
        value: false,
        title: '',
        collection: 'sale'
      }
    }
  },
  methods: {
    openDialog (collection) {

      this.dialog.title = collection
      this.dialog.collection = collection
      this.dialog.value = true
    },
    newSave () {
      this.dialog.value = false
    }
  }
}
</script>